import React from 'react';
import ReactDOM from 'react-dom/client';
import './styles/index.scss';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {HashRouter} from 'react-router-dom';
import {Provider} from 'react-redux';
import {store} from './store';
// @ts-ignore
import {Helmet} from "react-helmet";

const root = ReactDOM.createRoot(
    document.getElementById('root') as HTMLElement
);
root.render(
    <React.StrictMode>
        <Provider store={store}>
            <HashRouter>
                <Helmet htmlAttributes>
                    {/* Основные мета-теги Open Graph */}
                    <meta property="og:title" content="Vinyl Record Store" />
                    <meta property="og:description" content="Wide selection of vinyl records from various genres and artists. Order your favorite albums now!" />
                    <meta property="og:url" content="URL of your page" />
                    <meta property="og:type" content="website" />

                    {/* Дополнительные мета-теги Open Graph */}
                    <meta property="og:site_name" content="Vinyl Record Store" />
                    <meta property="og:locale" content="en_US" />

                    {/* Мета-теги Twitter Cards (опционально) */}
                    <meta name="twitter:card" content="summary" />
                    <meta name="twitter:title" content="Vinyl Record Store" />
                    <meta name="twitter:description" content="Wide selection of vinyl records from various genres and artists. Order your favorite albums now!" />
                </Helmet>
                <App/>
            </HashRouter>
        </Provider>
    </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
